.b-helpful-info__nav {
  float: none;
  margin-left: 23px; }
  .b-helpful-info__nav .icon-svg {
    font-size: 26px;
    color: #0b1226; }
  .b-helpful-info__nav .prev,
  .b-helpful-info__nav .next {
    display: inline-block;
    cursor: pointer; }
  .b-helpful-info__nav .prev {
    margin-right: 16px; }
  
  .b-helpful-info__nav .next {
    margin-left: 16px; }
  .disabled.b-helpful-info__nav {
    display: none; }
  @media (min-width: 768px) {
    .b-helpful-info__nav {
      float: right; } }

.b-helpful-info__title {
  font-weight: bold; }

.b-helpful-info__item {
  border: 1px solid #e8e8e8;
  margin-bottom: 11px;
  position: relative;
  background: #fff;
  transition: box-shadow .3s; }
  @media (min-width: 768px) {
    .b-helpful-info__item {
      margin-bottom: 23px; } }

.b-helpful-info__item:hover {
  box-shadow: 0 0 26px rgba(0, 0, 0, 0.13); }

.b-helpful-info__pic {
  position: relative;
  display: block;
  overflow: hidden;
  height: 200px; }

.b-helpful-info__pic img {
  position: absolute;
  left: -9999px;
  right: -9999px;
  top: -9999px;
  bottom: -9999px;
  margin: auto;
  max-width: none; }

.b-helpful-info .owl-carousel .owl-item img {
  width: auto; }

.b-helpful-info .owl-carousel .prev,
.b-helpful-info .owl-carousel .next {
  margin-top: -8px; }

.b-helpful-info__datainner {
  margin-top: 11px;
  height: 150px;
  overflow: hidden; }
  .b-helpful-info__datainner h5 {
    margin-top: 0px;
    margin-bottom: 0.5rem; }

.b-helpful-info__date, .b-helpful-info__desc {
  color: #555555;
  font-size: 1em; }

.b-helpful-info__data {
  padding: 9px 38px 25px 38px; }

.b-helpful-info__date {
  display: inline-block; }

.b-helpful-info__name {
  color: #202020; }

.b-helpful-info__name:hover, .b-helpful-info__active {
  color: #202020; }

.b-helpful-info__desc {
  padding: 5px 0; }

.b-helpful-info__tag {
  position: absolute;
  z-index: 90;
  color: #fff;
  padding: 4px 8px;
  font-size: 13px;
  font-weight: 400;
  left: 12px;
  top: 0;
  letter-spacing: 0.2px; }

.b-helpful-info__tag:hover, .b-helpful-info__tag:active, .b-helpful-info__tag:focus {
  color: #fff;
  text-decoration: none; }

.b-helpful-info__tag:hover:before {
  content: "";
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1; }

.b-helpful-info__tools {
  margin-top: 30px;
  margin-bottom: 20px;
  text-align: center; }

.b-helpful-info__nav, .b-helpful-info__showall {
  display: inline-block;
  vertical-align: middle; }

.b-helpful-info__showall {
  font-weight: bold; }

@media (min-width: 480px) and (max-width: 991px) {
  .b-helpful-info__items.row > div {
    width: 50%;
    float: left; } }
